body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  height: 1080px;
  width: 1920px;
  background-image: url(/static/media/background.8d50ab2f.jpeg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat; }

h1 {
  color: red; }

.jumbotron {
  opacity: 70%; }

form {
  width: 50%; }

button, span {
  cursor: pointer; }

h5 {
  color: black; }

.container {
  min-height: 100vh;
  position: relative; }

.card {
  width: 50%; }

.profile {
background-color: darkgrey;
border-radius: 10px;
padding: 20px;
height: 300px;
width: 250px;
text-align: center;
}
.history {
background-color: darkgrey;
border-radius: 10px;
padding: 20px;
height: 400px;
width: 400px;
text-align: center;
float: right;
}

